<template>
  <div id="usermanage">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="query" clearable @clear="getUserData">
            <el-button slot="append" icon="el-icon-search" @click="getSearchData" @keyup.enter.native="getSearchData"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addUser"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userData" style="width: 100%" border>
          <el-table-column prop="id" label=""></el-table-column>
          <el-table-column prop="department.name" label=""></el-table-column>
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column prop="telephone" label=""></el-table-column>
          <el-table-column prop="email" label=""></el-table-column>
          <el-table-column prop="authority" label="">
            <template slot-scope="scope">
              <span v-if="scope.row.authority == 0"></span>
              <span v-if="scope.row.authority == 1"></span>
              <span v-if="scope.row.authority == 2"></span>
            </template>
          </el-table-column>
          <el-table-column  label="" width="180px">
            <template slot-scope="scope">
              <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="updateInfo(scope.row.id)"></el-button>
              </el-tooltip>
               <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                <el-button type="info" icon="el-icon-delete" size="mini" @click="deleteInfo(scope.row.id)"></el-button>
              </el-tooltip>
              <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                <el-button type="warning" icon="el-icon-setting" size="mini" @click="editPassword(scope.row.id)"></el-button>
              </el-tooltip>
              <el-dialog title="" :visible.sync="editUser" width="30%" @close="editFormClose">
                <el-form ref="editFormRef" :rules="editFormRules" :model="editForm" label-width="80px" :label-position="labelPosition">
                  <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="email">
                    <el-input v-model="editForm.email"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="telephone">
                    <el-input v-model="editForm.telephone"></el-input>
                  </el-form-item>
                  <el-form-item label="">
                    <el-select v-model="editForm.authority" placeholder="">
                      <el-option label="" :value="0"></el-option>
                      <el-option label="" :value="1"></el-option>
                      <el-option label="" :value="2"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="" prop="department">
                    <el-select v-model="editForm.department.id" placeholder="">
                      <el-option  :value="item.id" :label="item.name"  v-for="item in apartList" :key="item.id"></el-option>
                    </el-select>
                  </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="editUser = false"> </el-button>
                  <el-button type="primary" @click="editUserInfo"> </el-button>
                </span>
              </el-dialog>
              <el-dialog title="" :visible.sync="editPwd" width="50%" @close="editPwdClose">
                <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="90px" :label-position="labelPosition">
                  <el-form-item label="   :" prop="pass">
                    <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
                  </el-form-item>
                  <el-form-item label="   :" prop="checkPass">
                    <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
                  </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="resetForm('ruleForm')"></el-button>
                  <el-button @click="editPwd = false"> </el-button>
                  <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
                </span>
              </el-dialog>
            </template>
          </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
      :current-page="pagenum" :page-sizes="[1, 2, 5, 10]"
      :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper"
      :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import secret from '../secret/secret'
export default {
  data () {
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass')
        }
        callback()
      }
    }
    const validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    const email = (rule, value, callback) => {
      const email = /^([a-zA-Z0-9]+[-_.]?)+@[a-zA-Z0-9]+\.[a-z]+$/
      if (!email.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    const tel = (rule, value, callback) => {
      const tel = /^[1][3-9][0-9]{9}$/
      if (!tel.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      labelPosition: 'left',
      userData: [],
      query: '',
      pagenum: 1,
      pagesize: 2,
      total: 0,
      editUser: false,
      editPwd: false,
      editForm: {
        name: '',
        email: '',
        telephone: '',
        authority: '',
        department: {
          id: ''
        }
      },
      apartList: [],
      ruleForm: {
        pass: '',
        checkPass: ''
      },
      rules: {
        pass: [
          { validator: validatePass, trigger: 'blur' },
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 18, message: ' 6  18 ', trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' },
          { min: 6, max: 18, message: ' 6  18 ', trigger: 'blur' }
        ]
      },
      editFormRules: {
        name: [
          { required: true, message: '!', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        email: [
          { validator: email, trigger: 'blur' }
        ],
        telephone: [
          { validator: tel, trigger: 'blur' }
        ],
        department: [
          { required: true, message: '!', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.checkAuthority()
    // this.getUserData() // 
    this.getApartList() // 
    this.getSearchData() // 
  },
  methods: {
    // 
    addUser () {
      this.$router.push('/adduser')
    },
    // ()
    getUserData () {
      this.$get('/user', { pagenum: this.pagenum, pagesize: this.pagesize })
        .then(response => {
          if (response.data.meta.status === 200) {
            this.userData = response.data.data.rows
            this.total = response.data.data.total
          }
        })
        .catch(err => {
          this.$message.error(err)
        })
    },
    //  pagesize 
    handleSizeChange (newSize) {
      this.pagesize = newSize
      this.getUserData()
    },
    //   
    handleCurrentChange (newPage) {
      this.pagenum = newPage
      this.getUserData()
    },
    updateInfo (id) {
      this.editUser = true
      this.$get('/user/' + id)
        .then(response => {
          this.editForm = response.data.data
          this.editForm.department.id = response.data.data.department.id
        })
        .catch(err => {
          this.$message.error(err.data.meta.msg)
        })
    },
    // 
    editFormClose () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editPwdClose () {
      this.$refs.ruleForm.resetFields()
    },
    editPassword (id) {
      window.sessionStorage.setItem('curUserid', id)
      this.editPwd = true
    },
    editUserInfo () {
      this.$refs.editFormRef.validate(valid => {
        if (!valid) return
        // 
        this.$put('/user/update', this.editForm)
          .then((response) => {
            if (response.meta.status === 200) {
              this.editUser = false
              this.$message.success('')
              this.getSearchData(JSON.parse(window.sessionStorage.getItem('temporaryUserInfo')))
            }
          })
          .catch(err => {
            this.$message.error(err)
          })
      })
    },
    // 
    getApartList () {
      this.$get('/department/getAll', {})
        .then((response) => {
          if (response.data.meta.status === 200) {
            this.apartList = response.data.data
          }
        })
        .catch(err => err)
    },
    async deleteInfo (id) {
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .catch(err => err)
      if (confirmResult !== 'confirm') {
        this.$message.info('')
      }
      this.$del('' + id, {})
        .then(response => {
          if (response.data.meta.status === 204) {
            this.$message.success(response.data.meta.msg)
            this.getSearchData(JSON.parse(window.sessionStorage.getItem('temporaryUserInfo')))
          }
        })
        .catch(err => err)
    },
    // 
    getSearchData () {
      this.$get('/user/name', { keyword: this.query, pagenum: this.pagenum, pagesize: this.pagesize, query: this.query })
        .then(response => {
          if (response.data.meta.status === 200) {
            this.userData = response.data.data.rows
            this.total = response.data.data.total
            this.temporaryUserInfo = {
              keyword: this.query,
              pagenum: this.pagenum,
              pagesize: this.pagesize,
              query: this.query
            }
            // 
            window.sessionStorage.setItem('temporaryUserInfo', JSON.stringify(this.temporaryUserInfo))
          }
        })
        .catch(err => {
          this.$message.error(err)
        })
    },
    // 
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.ruleForm.pass = secret.encrypt(this.ruleForm.pass, 'ABGHNJHGSHUYG12')
          this.$put('/user/update/password', { id: window.sessionStorage.getItem('curUserid'), password: this.ruleForm.pass })
            .then(response => {
              this.editPwd = false
              this.$message.success(response.meta.msg)
            })
            .catch(err => {
              this.$message.error(err.meta.msg)
            })
        }
      })
    },
    // 
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    // 
    checkAuthority () {
      // 
      if (window.sessionStorage.getItem('userAuthority') === '2') {
        this.$router.push('/home')
        this.$message.error('')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 15px;
  .el-table {
    margin-top: 10px;
  }
  .el-pagination {
    margin-top: 10px;
    text-align: center;
  }
}
</style>
