<template>
  <div id="add">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/add' }"></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="box">
      <!--  -->
      <el-card>
        <el-form ref="searchInputRef" :inline="true" :model="searchInput" label-width="90px" :label-position="labelPosition">
          <el-form-item label=":" prop="grade_protect_level">
            <el-input  size="small" v-model="searchInput.grade_protect_level"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="review_time">
            <el-date-picker v-model="searchInput.review_time" type="daterange" align="right" unlink-panels size="small"
              range-separator="" start-placeholder="" end-placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item label=":" prop="import_time">
            <el-date-picker v-model="searchInput.import_time" type="daterange" align="right" unlink-panels size="small"
              range-separator="" start-placeholder="" end-placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item label=":" prop="keywords">
            <el-input v-model="searchInput.keywords" size="small"></el-input>
          </el-form-item>
        </el-form>
        <div class="box">
          <el-button type="primary"  @click="searchInfo"></el-button>
          <el-button type="info"  @click="resetLoginForm"></el-button>
        </div>
      </el-card>
      <!--  -->
      <el-card>
        <el-row :gutter="60">
          <el-col :xs="{span:24,offset:0}" :sm="{span:7,offset:0}" :md="{span:2,offset:1}">
            <el-button type="primary" @click="addInfo"></el-button>
          </el-col>
          <el-col :xs="{span:24,offset:0}" :sm="{span:7,offset:0}" :md="{span:2,offset:1}">
            <el-upload
              class="upload-demo"
              ref="upload"
              action="#"
              :limit="3"
              :http-request="uploadOk"
              :before-upload="beforeAvatarUpload"
              :auto-upload="true">
              <el-button slot="trigger" size="large" type="primary"></el-button>
            </el-upload>
          </el-col>
          <el-col :xs="{span:24,offset:0}" :sm="{span:7,offset:0}" :md="{span:2,offset:1}">
            <el-button type="primary" @click="handleDownload"></el-button>
          </el-col>
        </el-row>
        <el-table :data="systemData" style="width: 100%" border>
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column prop="department" label=""></el-table-column>
          <el-table-column prop="reviewer" label=""></el-table-column>
          <el-table-column prop="review_state" label="">
            <template slot-scope="scope">
              <span v-if="scope.row.review_state == 0"></span>
              <span v-if="scope.row.review_state == 1"></span>
              <span v-if="scope.row.review_state == 2"></span>
              <span v-if="scope.row.review_state == 3"></span>
            </template>
          </el-table-column>
          <el-table-column prop="pass_time" label=""></el-table-column>
          <el-table-column  label="" width="120px">
            <template slot-scope="scope">
              <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="editSystemInfo(scope.row.id)"></el-button>
              </el-tooltip>
               <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                <el-button type="info" icon="el-icon-delete" size="mini" @click="deleteSystemInfo(scope.row.id)"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
          :current-page="pagenum" :page-sizes="[1, 2, 5, 10]"
          :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </el-card>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      labelPosition: 'left',
      // 
      searchInput: {
        grade_protect_level: '',
        review_time: [],
        import_time: [],
        keywords: ''
      },
      // 
      temporarySearch: {
        grade_protect_level: '',
        review_time: [],
        import_time: [],
        keywords: ''
      },
      systemData: [], // 
      pagenum: 1,
      pagesize: 2,
      total: 0
    }
  },
  created () {
    this.searchInfo()
    // ,
    if (JSON.parse(window.sessionStorage.getItem('temporarySearch'))) {
      this.searchInput.grade_protect_level = JSON.parse(window.sessionStorage.getItem('temporarySearch')).grade
      this.$get('/sysInfo/findByInfo', JSON.parse(window.sessionStorage.getItem('temporarySearch')))
        .then(response => {
          this.systemData = response.data.data.rows
          this.total = response.data.data.total
        })
    }
  },
  methods: {
    // 
    searchInfo () {
      this.$get('/sysInfo/findByInfo', {
        pagenum: this.pagenum,
        pagesize: this.pagesize,
        grade: this.searchInput.grade_protect_level,
        review_time: this.searchInput.review_time,
        import_time: this.searchInput.import_time,
        keywords: this.searchInput.keywords
      })
        .then(response => {
          this.systemData = response.data.data.rows
          this.total = response.data.data.total
          this.temporarySearch = {
            pagenum: this.pagenum,
            pagesize: this.pagesize,
            grade: this.searchInput.grade_protect_level,
            review_time: this.searchInput.review_time,
            import_time: this.searchInput.import_time,
            keywords: this.searchInput.keywords
          }
          window.sessionStorage.setItem('temporarySearch', JSON.stringify(this.temporarySearch))
        })
    },
    // 
    resetLoginForm () {
      window.sessionStorage.removeItem('temporarySearch')
      this.searchInput = {}
      this.systemData = ''
      this.$refs.searchInputRef.resetFields()
    },
    // 
    addInfo () {
      // this.$router.push('/infounit')
      this.$router.push('/info')
    },
    // 
    editSystemInfo (id) {
      window.sessionStorage.setItem('editsystemid', id)
      this.$router.push('/edit')
      this.searchInfo(window.sessionStorage.getItem('temporarySearch'))
    },
    // 
    async deleteSystemInfo (id) {
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      this.$delete('/sysInfo/' + id, {})
        .then(response => {
          if (response.meta.status === 204) {
            this.$message.success(response.meta.msg)
            // ()
            this.$get('/sysInfo/findByInfo', JSON.parse(window.sessionStorage.getItem('temporarySearch')))
              .then(response => {
                this.systemData = response.data.data.rows
                this.total = response.data.data.total
                this.searchInfo(window.sessionStorage.getItem('temporarySearch'))
              })
          }
        })
        .catch(err => err)
    },
    //  pagesize 
    handleSizeChange (newSize) {
      this.pagesize = newSize
      this.searchInfo()
    },
    //   
    handleCurrentChange (newPage) {
      this.pagenum = newPage
      this.searchInfo()
    },
    // Excel
    handleDownload () {
      axios({
        method: 'get',
        url: 'http://10.11.37.162:8080/grade_protection/sysInfo/getExcel',
        responseType: 'blob'
      })
        .then((res) => {
          const url = window.URL.createObjectURL(new Blob([res.data]))
          const link = document.createElement('a')
          link.style.display = 'none'
          link.href = url
          link.setAttribute('download', this.$route.meta.title + '.xlsx')
          document.body.appendChild(link)
          link.click()
        })
        .catch(() => {
          this.$message.error('!')
        })
    },
    // Excel
    // 
    beforeAvatarUpload (file) {
      const Xls = file.name.split('.')
      const ls = file.name.split('.')
      if (Xls[1] === 'xls' || ls[1] === 'xlsx') {
        // return file
        console.log(file)
      } else {
        this.$message.error(' xls/xlsx !')
        return false
      }
    },
    // 
    uploadOk (val) {
      const fd = new FormData()
      fd.append('file', val.file) // 'file'
      axios({
        method: 'post',
        url: 'http://112.124.46.193:8080/grade_protection/sysInfo/excel',
        headers: { 'Content-Type': 'multipart/form-data; boundary=----WebKitFormBoundaryXUIhYHlAmsiYAKUG' },
        data: fd
      })
        .then(response => {
          console.log(response)
          if (response.data.meta.status === 201) {
            this.$message.success(response.data.meta.msg)
          } else {
            console.log(response)
            this.$message.error('')
          }
        })
    }
  }
}
</script>

<style lang="less" scoped>
.box {
  margin-top: 15px;
  .el-card:first-child {
    text-align: center;
    .el-input {
      width: 350px;
    }
    .box {
      text-align: center;
    }
  }
  .el-card:last-child {
    margin-top: 10px;
    .el-table {
      margin-top: 10px;
    }
    .el-pagination {
      margin-top: 10px;
      text-align: center;
    }
  }
}
</style>
