    margin-top: 15px;
    .el-form {
      margin-top: 10px;
      .el-input {
        width: 300px;
      }
      .single .el-radio {
        padding-top: 12px;
      }
      .el-checkbox {
        margin-left: 100px;
      }
    }
    .box {
