<template>
  <div id="welcome">
    <!--  -->
    <el-card shadow="hover">
      <el-row :gutter="20">
        <el-col :xs="15" :sm="13" :md="12" :lg="12" :xl="12">
          <img src="../assets/Avatar.png" alt="">
          <span>&nbsp;<b style="letter-spacing:1px">{{name}}</b>&nbsp;</span>
        </el-col>
        <el-col :xs="9" :sm="11" :md="12" :lg="12" :xl="12">
          <div>
            <p>:{{uncheceked}}</p>
            <p>:{{pass}}</p>
          </div>
        </el-col>
      </el-row>
    </el-card>
    <!--  -->
    <el-card>
        <el-container>
          <!--  -->
          <el-aside width="400px">
            <div id="pie" :style="{width: '400px', height: '400px'}"></div>
          </el-aside>
          <!--  -->
          <el-main>
            <p>&nbsp;>></p>
            <div id="parent" class="parent">
              <ul id="child1"  class="child">
                <li v-for='(item, index) in items' :key="index">{{item.importTime}}{{item.departmentName}}{{item.importerName}}{{item.systemName}}</li>
              </ul>
              <ul id="child1"  class="child">
                <li v-for='(item, index) in items' :key="index">{{item.importTime}}{{item.departmentName}}{{item.importerName}}{{item.systemName}}</li>
              </ul>
              <ul id="child1"  class="child">
                <li v-for='(item, index) in items' :key="index">{{item.importTime}}{{item.departmentName}}{{item.importerName}}{{item.systemName}}</li>
              </ul>
            </div>
          </el-main>
        </el-container>
    </el-card>
  </div>
</template>

<script type="text/javascript">
export default {
  data () {
    return {
      name: '',
      uncheceked: '',
      pass: '',
      animate: false,
      items: []
    }
  },
  created () {
    // 
    this.$get('/user/' + window.sessionStorage.getItem('userid'))
      .then(response => {
        this.name = response.data.data.name
      })
    // 
    this.drawPie()
    //  
    this.$get('/sysInfo/countByAuthority')
      .then(response => {
        this.uncheceked = response.data.data[0]
        this.pass = response.data.data[1]
      })
    // 
    this.$get('/sysInfo/carouselData')
      .then(response => {
        this.items = response.data.data
      })
    // 
    this.$nextTick(() => {
      const child1 = document.getElementById('child1')
      // const child2 = document.getElementById('child2')
      const parent = document.getElementById('parent')
      // child2.innerHTML = child1.innerHTML
      // console.log(child2.innerHTML)
      setInterval(() => {
        if (parent.scrollTop >= child1.scrollHeight) {
          parent.scrollTop = 0
        } else {
          parent.scrollTop++
        }
      }, 20)
    })
  },
  methods: {
    // 
    drawPie () {
      this.$get('/sysInfo/groupGradeNum')
        .then(response => {
          const gradenum = response.data.data
          // chartscharts
          const myChart = this.$echarts.init(document.getElementById('pie'))
          // 
          myChart.setOption({
            title: {
              text: '',
              left: 'center' // 
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b} : ({d}%)'
            },
            legend: {
              bottom: 10,
              left: 'center', // 
              data: ['', '', '']
            },
            series: [
              {
                type: 'pie',
                radius: '65%',
                center: ['50%', '50%'],
                selectedMode: 'single',
                data: [
                  { value: gradenum.grade2num, name: '' },
                  { value: gradenum.grade3num, name: '' },
                  { value: gradenum.grade4num, name: '' }
                ],
                emphasis: {
                  itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
                }
              }
            ]
          })
        })
        .catch(err => err)
    }
  }
}
</script>

<style lang="less" scoped>
.el-card:first-child {
  .el-row {
    position: relative;
    .el-col:first-child {
      span {
        margin-left: 15px;
      }
      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        vertical-align: middle;
      }
    }
    .el-col:last-child {
      div {
        position: absolute;
        right: 40px;
        top: -18px;
      }
    }
  }
}
.el-card:last-child {
  .el-aside {
    margin-top: 20px;
  }
  .el-main {
    p {
      margin-top: 0;
      margin-left: 15px;
      color: cadetblue;
    }
    .parent {
      width: 600px;
      height: 380px;
      margin: 0 auto;
      // background: rgba(100, 100, 100, 0.1);
      overflow: hidden;
		}
		/**/
    .child {
      height: auto;
		}
		.child li {
      list-style: none;
			height: auto;
			margin: 12px 10px;
      color: #333;
		}
  }
}
</style>
