<script>
export default {
  data () {
    return {
      labelPosition: 'left',
      checkDingjiForm: {}
    }
  },
  created () {
    this.getSystems()
  },
  methods: {
    fowardPage () {
      this.$router.push('/checksystem')
    },
    nextPage () {
      this.$router.push('/checkcommit')
    },
    getSystems () {
      const id = window.sessionStorage.getItem('systemid')
      this.$get('/sysInfo/findById/' + id)
        .then(response => {
          this.checkDingjiForm = response.data.data
        })
    }
  }
}
</script>

