<template>
  <div id="adduser">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/usermanage' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/adduser' }"></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-form ref="adduserFormRef" :rules="adduserRules" :model="adduserForm" label-width="80px" :label-position="labelPosition">
        <el-form-item label="" prop="name">
          <el-input v-model="adduserForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="adduserForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="adduserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone">
          <el-input v-model="adduserForm.telephone"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="adduserForm.authority" placeholder="">
            <el-option label="" value="0"></el-option>
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="department.id" :rules="[{ required: true, message: '', trigger: 'blur' }]">
          <el-select v-model="adduserForm.department.id" placeholder="">
            <el-option  :label="item.name" :value="item.id" v-for="item in apartList" :key="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="commit"></el-button>
          <el-button type="primary" @click="reset"></el-button>
          <el-button @click="cancel"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    const pwd = (rule, value, callback) => {
      const pwd = /^(?![\d]+$)(?![a-zA-Z]+$)(?![^\da-zA-Z]+$)([^\u4e00-\u9fa5\s]){6,18}$/
      if (!pwd.test(value)) {
        callback(new Error('6-18'))
      } else {
        callback()
      }
    }
    const email = (rule, value, callback) => {
      const email = /^([a-zA-Z0-9]+[-_.]?)+@[a-zA-Z0-9]+\.[a-z]+$/
      if (!email.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    const tel = (rule, value, callback) => {
      const tel = /^[1][3-9][0-9]{9}$/
      if (!tel.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      labelPosition: 'left',
      adduserForm: {
        name: '',
        password: '',
        email: '',
        telephone: '',
        authority: '2',
        department: {
          id: ''
        }
      },
      apartList: [],
      adduserRules: {
        name: [
          { required: true, message: '!', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { validator: pwd, trigger: 'blur' }
        ],
        email: [
          { validator: email, trigger: 'blur' }
        ],
        telephone: [
          { validator: tel, trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getApartList()
  },
  methods: {
    reset () {
      this.$refs.adduserFormRef.resetFields()
    },
    cancel () {
      this.$router.push('/usermanage')
    },
    commit () {
      console.log('commit...')
      console.log(this.adduserForm.department)
      this.$post('/user', this.adduserForm)
        .then((response) => {
          if (response.data.meta.status === 201) {
            this.$message.success('')
            this.$router.push('/usermanage')
          }
        })
        .catch(err => err)
    },
    getApartList () {
      this.$get('/department/getAll', {})
        .then((response) => {
          if (response.data.meta.status === 200) {
            console.log(response.data.data)
            this.apartList = response.data.data
          }
        })
        .catch(err => err)
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 10px;
  .el-input {
    width: 300px;
  }
}
</style>
