</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      // v-show
      unitFlag: true,
      systemFlag: false,
      levelFlag: false,
      // 
      labelPosition: 'left',
      // infoUnit
      addApartForm: {
        name: '',
        principal_name: '',
        principal_telephone: '',
        principal_title: '',
        principal_office_telephone: ''
      },
      // infoSystem
      addSystemForm: {
        name: '',
        grade_protect_level: '',
        business_type: '',
        business_desc: '',
        service_area: '',
        service_obj: '',
        network: '',
        sys_interconnect: '',
        safety_num: '',
        domestic_safety_num: '',
        network_num: '',
        domestic_network_num: '',
        operate_sys_num: '',
        domestic_operate_sys_num: '',
        database_num: '',
        domestic_database_num: '',
        server_num: '',
        domestic_server_num: '',
        rank_evaluation: '',
        risk_assessment: '',
        disaster_recover: '',
        emergency_response: '',
        sys_integration: '',
        security_advisory: '',
        security_train: '',
        evaluate_firm_name: '',
        put_into_time: ''
      },
      systemRules: {
        name: [
          { required: true, message: '!', trigger: 'blur' },
          { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }
        ]
      },
      // infoLevel
      addDingjiForm: {
        business_info_level: '',
        sys_service_level: '',
        info_sys_level: '',
        rank_time: '',
        expert_review: '',
        competent_authority_name: '',
        sys_rate_report: ''
      }
    }
  },
  created () {
    this.getInfoDw()
  },
  methods: {
    // infoUnit
    returnAdd () {
      this.$router.push('/add')
    },
    nextPageSystem () {
      this.unitFlag = false
      this.systemFlag = true
    },
    getInfoDw () {
      this.$get('/user/' + window.sessionStorage.getItem('userid'))
        .then(response => {
          this.addApartForm = response.data.data.department
          window.sessionStorage.setItem('apartid', this.addApartForm.id)
          window.localStorage.setItem('infoUnit', JSON.stringify(this.addApartForm))
        })
        .catch(err => {
          this.$message.error(err.data.meta.msg)
        })
    },
    // infoSystem
    fowardPageUnit () {
      this.unitFlag = true
      this.systemFlag = false
    },
    nextPageLevel () {
      this.levelFlag = true
      this.systemFlag = false
    },
    // infoLevel
    fowardPageSystem () {
      this.levelFlag = false
      this.systemFlag = true
    },
    temporarySave () {
      const obj = Object.assign(this.addApartForm, this.addSystemForm, this.addDingjiForm)
      obj.department_id = window.sessionStorage.getItem('apartid')
      obj.review_state = 0
      this.$post('/sysInfo', obj)
    },
    commit () {
      const obj = Object.assign(this.addApartForm, this.addSystemForm, this.addDingjiForm)
      obj.department_id = window.sessionStorage.getItem('apartid')
      obj.review_state = 1
      this.$post('/sysInfo', obj)
        .then(response => {
          if (response.data.meta.status === 201) {
            window.sessionStorage.setItem('newSystemId', response.data.data.id)
            this.$message.success(response.data.meta.msg)
          }
        })
    },
    uploadOk (val) {
      const fd = new FormData()
      fd.append('file', val.file) // 'file'
      axios({
        method: 'post',
        url: 'http://112.124.46.193:8080/grade_protection/sysInfo/annex/' + window.sessionStorage.getItem('newSystemId'),
        headers: { 'Content-Type': 'multipart/form-data; boundary=----WebKitFormBoundaryXUIhYHlAmsiYAKUG' },
        data: fd
      })
        .then(response => {
          if (response.data.meta.status === 201) {
            this.$message.success(response.data.meta.msg)
          } else {
            this.$message.error('')
          }
        })
        .catch(() => {
          this.$message.error('')
        })
    }
  }
}
</script>

