<template>
  <div id="usermanage">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="query" clearable @clear="getApartmentData">
            <el-button slot="append" icon="el-icon-search" @click="getSearchData" @keyup.enter.native="getSearchData"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addApartment"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="apartmentData" style="width: 100%" border>
          <el-table-column prop="id" label=""></el-table-column>
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column prop="principal_name" label=""></el-table-column>
          <el-table-column prop="principal_telephone" label=""></el-table-column>
          <el-table-column prop="principal_office_telephone" label=""></el-table-column>
          <el-table-column prop="principal_email" label=""></el-table-column>
          <el-table-column  label="" width="120px">
            <template slot-scope="scope">
              <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="updateInfo(scope.row.id)"></el-button>
              </el-tooltip>
              <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                <el-button type="info" icon="el-icon-delete" size="mini" @click="deleteInfo(scope.row.id)"></el-button>
              </el-tooltip>
              <!--  -->
              <el-dialog title="" :visible.sync="editApartment" width="50%" @close="editFormClose">
                <el-form ref="editFormRef" :rules="editFormRules" :model="editForm" label-width="130px" :label-position="labelPosition">
                  <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="principal_name">
                    <el-input v-model="editForm.principal_name"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="principal_telephone">
                    <el-input v-model="editForm.principal_telephone"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="principal_email">
                    <el-input v-model="editForm.principal_email"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="principal_title">
                    <el-input v-model="editForm.principal_title"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="principal_office_telephone">
                    <el-input v-model="editForm.principal_office_telephone"></el-input>
                  </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="editApartment = false"> </el-button>
                  <el-button type="primary" @click="editApartmentInfo"> </el-button>
                </span>
              </el-dialog>
            </template>
          </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
      :current-page="pagenum" :page-sizes="[1, 2, 5, 10]"
      :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper"
      :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    const tel = (rule, value, callback) => {
      const tel = /^[1][3-9][0-9]{9}$/
      if (!tel.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    const email = (rule, value, callback) => {
      const email = /^([a-zA-Z0-9]+[-_.]?)+@[a-zA-Z0-9]+\.[a-z]+$/
      if (!email.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    const phone = (rule, value, callback) => {
      const phone = /0\d{2,3}-\d{7,8}/
      if (!phone.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    const title = (rule, value, callback) => {
      const title = /^[\u0391-\uFFE5A-Za-z]+$/
      if (!title.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      labelPosition: 'left',
      apartmentData: [],
      query: '',
      pagenum: 1,
      pagesize: 2,
      total: 0,
      // 
      editApartment: false,
      editForm: {},
      editFormRules: {
        name: [
          { required: true, message: '!', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        principal_name: [
          { required: true, message: '!', trigger: 'blur' },
          { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
        ],
        principal_telephone: [
          { validator: tel, trigger: 'blur' }
        ],
        principal_email: [
          { validator: email, trigger: 'blur' }
        ],
        principal_office_telephone: [
          { validator: phone, trigger: 'blur' }
        ],
        principal_title: [
          { validator: title, trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    // this.getApartmentData() // 
    this.checkAuthority()
    this.getSearchData() // 
  },
  methods: {
    addApartment () {
      this.$router.push('/addapartment')
    },
    // 
    getApartmentData () {
      this.$get('/department', { pagenum: this.pagenum, pagesize: this.pagesize, query: this.query })
        .then(response => {
          if (response.data.meta.status === 200) {
            this.apartmentData = response.data.data.rows
            this.total = response.data.data.total
          }
        })
        .catch(err => {
          this.$message.error(err)
        })
    },
    //  pagesize 
    handleSizeChange (newSize) {
      this.pagesize = newSize
      this.getApartmentData()
    },
    //   
    handleCurrentChange (newPage) {
      this.pagenum = newPage
      this.getApartmentData()
    },
    updateInfo (id) {
      this.editApartment = true
      this.$get('/department/findById/' + id, {})
        .then(response => {
          this.editForm = response.data.data
        })
        .catch(err => {
          console.log(err)
        })
    },
    editFormClose () {
      this.$refs.editFormRef.resetFields()
    },
    editApartmentInfo () {
      this.$refs.editFormRef.validate(valid => {
        if (!valid) return
        // 
        this.$put('/department/' + this.editForm.id, this.editForm)
          .then(response => {
            if (response.meta.status === 200) {
              this.editApartment = false
              this.$message.success('')
              this.getSearchData(JSON.parse(window.sessionStorage.getItem('temporaryApartInfo')))
            }
          })
          .catch(err => {
            console.log(err)
            this.$message.error('')
          })
      })
    },
    async deleteInfo (id) {
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      this.$delete('/department/' + id, {})
        .then(response => {
          if (response.meta.status === 204) {
            this.$message.success(response.meta.msg)
            this.getSearchData(JSON.parse(window.sessionStorage.getItem('temporaryApartInfo')))
          }
        })
        .catch(err => err)
    },
    getSearchData () {
      this.$get('/department/findByName', { pagenum: this.pagenum, pagesize: this.pagesize, query: this.query })
        .then(response => {
          if (response.data.meta.status === 200) {
            this.apartmentData = response.data.data.rows
            this.total = response.data.data.total
            this.temporaryApartInfo = {
              pagenum: this.pagenum,
              pagesize: this.pagesize,
              query: this.query
            }
            window.sessionStorage.setItem('temporaryApartInfo', JSON.stringify(this.temporaryApartInfo))
          }
        })
        .catch(err => {
          this.$message.error(err)
        })
    },
    // 
    checkAuthority () {
      // 
      if (window.sessionStorage.getItem('userAuthority') === '2') {
        this.$router.push('/home')
        this.$message.error('')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 15px;
  .el-table {
    margin-top: 10px;
  }
  .el-pagination {
    margin-top: 10px;
    text-align: center;
  }
}
</style>
