<template>
  <div id="adduser">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/apartmanage' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/addapartment' }"></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-form ref="addApartFormRef" :model="addApartForm" :rules="addapartRules" label-width="130px" :label-position="labelPosition">
        <el-form-item label="" prop="name">
          <el-input v-model="addApartForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="principal_name">
          <el-input v-model="addApartForm.principal_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="principal_telephone">
          <el-input v-model="addApartForm.principal_telephone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="principal_email">
          <el-input v-model="addApartForm.principal_email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="principal_title">
          <el-input v-model="addApartForm.principal_title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="principal_office_telephone">
          <el-input v-model="addApartForm.principal_office_telephone"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="commit"></el-button>
          <el-button type="info" @click="reset"></el-button>
          <el-button type="warning" @click="cancel"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    const tel = (rule, value, callback) => {
      const tel = /^[1][3-9][0-9]{9}$/
      if (!tel.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    const email = (rule, value, callback) => {
      const email = /^([a-zA-Z0-9]+[-_.]?)+@[a-zA-Z0-9]+\.[a-z]+$/
      if (!email.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    const phone = (rule, value, callback) => {
      const phone = /0\d{2,3}-\d{7,8}/ // 0731-88888888
      if (!phone.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    const title = (rule, value, callback) => {
      const title = /^[\u0391-\uFFE5A-Za-z]+$/
      if (!title.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      labelPosition: 'left',
      addApartForm: {
        name: '',
        principal_name: '',
        principal_telephone: '',
        principal_emailprincipal_email: '',
        principal_title: '',
        principal_office_telephone: ''
      },
      addapartRules: {
        name: [
          { required: true, message: '!', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        principal_name: [
          { required: true, message: '!', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        principal_telephone: [
          { validator: tel, trigger: 'blur' }
        ],
        principal_email: [
          { validator: email, trigger: 'blur' }
        ],
        principal_office_telephone: [
          { validator: phone, trigger: 'blur' }
        ],
        principal_title: [
          { validator: title, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    cancel () {
      this.$router.push('/apartmanage')
    },
    reset () {
      // 
      this.$refs.addApartFormRef.resetFields()
    },
    commit () {
      this.$refs.addApartFormRef.validate(valid => {
        if (!valid) return
        // post
        this.$post('/department', this.addApartForm)
          .then((response) => {
            console.log(response)
            if (response.data.meta.status === 201) {
              this.$message.success('')
            }
          })
          .catch(err => {
            console.log(err)
            this.$message.error('')
          })
      })
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 10px;
  .el-input {
    width: 300px;
  }
  .el-button:nth-child(2) {
    margin-left: 42px;
  }
  .el-button:nth-child(3) {
    margin-left: 42px;
  }
}
</style>
