</template>

<script>
export default {
  data () {
    return {
      labelPosition: 'left',
      checkXtForm: {}
    }
  },
  created () {
    this.getSystems()
  },
  methods: {
    fowardPage () {
      this.$router.push('/check')
    },
    nextPage () {
      this.$router.push('/checklevel')
    },
    getSystems () {
      this.$get('/sysInfo/findById/' + window.sessionStorage.getItem('systemid'))
        .then(response => {
          this.checkXtForm = response.data.data
        })
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 15px;
  .el-form {
    margin-top: 10px;
  }
  .el-input {
    width: 300px;
  }
  .box {
    text-align: center;
  }
}
</style>
