<template>
  <div id="statistics">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/statistics' }"></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <br>
      <div id="pillar" :style="{width: '1000px', height: '500px'}"></div>
      <br>
      <div class="box1">
        <div id="pie" :style="{width: '400px', height: '400px'}"></div>
        <div id="entryStatus" :style="{width: '400px', height: '400px'}"></div>
      </div>
      <br>
      <div class="box2">
        <div id="safetyProducts" :style="{width: '400px', height: '400px'}"></div>
        <div id="networkProducts" :style="{width: '400px', height: '400px'}"></div>
      </div>
      <br>
      <div class="box3">
        <div id="operatingSystems" :style="{width: '400px', height: '400px'}"></div>
        <div id="numberOfDatabases" :style="{width: '400px', height: '400px'}"></div>
      </div>
      <div id="numberOfServers" :style="{width: '400px', height: '400px'}"></div>
    </el-card>
  </div>
</template>

<script>
export default {
  created () {

  },
  mounted () {
    // 
    this.drawPie()
    this.drawPillar()
    this.drawEntryStatus()
    this.drawProduct('safety', 'safetyProducts', '', '', '')
    this.drawProduct('network', 'networkProducts', '', '', '')
    this.drawProduct('database', 'operatingSystems', '', '', '')
    this.drawProduct('server', 'numberOfDatabases', '', '', '')
    this.drawProduct('operate_sys', 'numberOfServers', '', '', '')
  },
  methods: {
    drawPie () {
      this.$get('/sysInfo/groupGradeNum')
        .then(response => {
          const gradenum = response.data.data
          // chartscharts
          const myChart = this.$echarts.init(document.getElementById('pie'))
          // 
          myChart.setOption({
            title: {
              text: '',
              left: 'center' // 
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b} : ({d}%)'
            },
            legend: {
              bottom: 10,
              left: 'center', // 
              data: ['', '', '']
            },
            series: [
              {
                type: 'pie',
                radius: '65%',
                center: ['50%', '50%'],
                selectedMode: 'single',
                data: [
                  { value: gradenum.grade2num, name: '' },
                  { value: gradenum.grade3num, name: '' },
                  { value: gradenum.grade4num, name: '' }
                ],
                emphasis: {
                  itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
                }
              }
            ]
          })
        })
        .catch(err => err)
    },
    drawPillar () {
      this.$get('/sysInfo/allDeptGradeStatus')
        .then(response => {
          const deptNameArr = []
          const grade2numArr = []
          const grade3numArr = []
          const grade4numArr = []
          response.data.data.forEach(element => {
            deptNameArr.push(element.deptName)
            grade2numArr.push(element.grade2num)
            grade3numArr.push(element.grade3num)
            grade4numArr.push(element.grade4num)
          })
          // chartscharts
          const myChart = this.$echarts.init(document.getElementById('pillar'))
          // 
          myChart.setOption({
            title: {
              text: '',
              top: -5,
              left: 'center' // 
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'shadow'
              }
            },
            legend: {
              top: 25,
              left: 'center',
              data: ['', '', '']
            },
            xAxis: [
              {
                type: 'category',
                data: deptNameArr,
                axisLabel: {
                  interval: 0,
                  rotate: 35 // 45
                }
              }
            ],
            yAxis: [
              {
                type: 'value'
              }
            ],
            series: [
              {
                name: '',
                type: 'bar',
                data: grade2numArr
              },
              {
                name: '',
                type: 'bar',
                data: grade3numArr
              },
              {
                name: '',
                type: 'bar',
                data: grade4numArr
              }
            ]
          })
        })
    },
    drawEntryStatus () {
      this.$get('/sysInfo/committedNum')
        .then(response => {
          // chartscharts
          const myChart = this.$echarts.init(document.getElementById('entryStatus'))
          // 
          myChart.setOption({
            title: {
              text: '',
              left: 'center' // 
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b} : ({d}%)'
            },
            legend: {
              bottom: 10,
              left: 'center', // 
              data: ['', '']
            },
            series: [
              {
                type: 'pie',
                radius: '65%',
                center: ['50%', '50%'],
                selectedMode: 'single',
                data: [
                  { value: response.data.data.committed_num, name: '' },
                  // { value: response.data.data.total_dept_num - response.data.data.committed_num, name: '' }
                  { value: response.data.data.total_dept_num, name: '' }
                ],
                emphasis: {
                  itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
                }
              }
            ]
          })
        })
        .catch(err => err)
    },
    drawProduct (type, id, text, val1, val2) {
      this.$get('/sysInfo/domesticNum/', { type: type })
        .then(response => {
          // chartscharts
          const myChart = this.$echarts.init(document.getElementById(id))
          // 
          myChart.setOption({
            title: {
              text: text,
              left: 'center' // 
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b} : ({d}%)'
            },
            legend: {
              bottom: 10,
              left: 'center', // 
              data: [val1, val2]
            },
            series: [
              {
                type: 'pie',
                radius: '65%',
                center: ['50%', '50%'],
                selectedMode: 'single',
                data: [
                  { value: response.data.data.domestic_num, name: val1 },
                  { value: response.data.data.import_num, name: val2 }
                ],
                emphasis: {
                  itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
                }
              }
            ]
          })
        })
        .catch(err => err)
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 15px;
  .box1, .box2, .box3{
    display: flex;
    justify-content: center;
    .div {
      flex: 1;
    }
  }
  #numberOfServers {
    margin: 0 auto;
  }
  #pillar {
    margin: 0 auto;
  }
}
</style>
